     url: '/web/v1/options/devices',\r\n                    type: 'POST',\r\n                    contentType: 'application/json',\r\n                    success: (response) => {\r\n                        if (response.data && response.data.list) {\r\n                            this.deviceOptions = response.data.list;\r\n                        }\r\n                    },\r\n                    error: (err) => {\r\n                        this.deviceOptions = [{\r\n                            value: 'All',\r\n                            name: 'All',\r\n                        }, {\r\n                            value: 'iOS',\r\n                            name: 'iOS',\r\n                        }, {\r\n                            value: 'Android',\r\n                            name: 'Android'\r\n                        }];\r\n                    }\r\n                });\r\n            },\r\n            search(){\r\n                let self = this;\r\n                let dateRange = self.searchParam.dateRange;\r\n                Common.ajax({\r\n                    url: \"/web/v1/operation/conversionRateByHour\",\r\n                    type: 'POST',\r\n                    contentType: \"application/json\",\r\n                    data: {\r\n                        page: self.currentPage,\r\n                        pageSize: self.pageSize,\r\n                        device: self.searchParam.device,\r\n                        beginTime: formatDate(dateRange[0], 'yyyy-MM-dd') + ' 00:00:00',\r\n                        endTime: formatDate(dateRange[1], 'yyyy-MM-dd') + ' 23:59:59',\r\n                    },\r\n                    success(response){\r\n                        if (response.data && response.data.list) {\r\n                            let data = response.data;\r\n                            self.tableData = data.list;\r\n                            self.total = data.pageUtil.count;\r\n                        }\r\n                    },\r\n                    error(err){\r\n                        let error = err.message || err;\r\n//            err.message && this.$alert(error, '', {\r\n//              type: \"error\",\r\n//              confirmButtonText: '知道了'\r\n//            });\r\n                        Notification.error({\r\n                            title: '错误',\r\n                            message: error\r\n                        });\r\n                    }\r\n                })\r\n            },\r\n            removeLabel(event){\r\n                event.stopPropagation();\r\n                this.activeName = 'first';\r\n                this.showDetail = false;\r\n\r\n            },\r\n            handleSizeChange(val) {\r\n                this.pageSize = val;\r\n                this.search();\r\n            },\r\n            handleCurrentChange(val) {\r\n                this.currentPage = val;\r\n                this.search();\r\n            },\r\n            handleClick(tab, event) {\r\n                let index = tab.index;\r\n                if (index == 0) {\r\n                    this.activeName = 'first';\r\n                }\r\n            }\r\n        },\r\n        created(){\r\n            //this.search();\r\n        },\r\n        mounted(){\r\n            //\r\n            this.getDevices();\r\n            this.search();\r\n        }\r\n    }\r\n</script>\r\n"]}]);
